.footer {

  &__body {
    display: flex;
    justify-content: space-between;

    @media (max-width:em(620)) {
      padding: rem(50) 0;
    }

    @include adaptiveValue("padding-top", 40, 20, 1490, 621, 0);
    @include adaptiveValue("padding-bottom", 40, 20, 1490, 621, 0);
  }

  &__action {
    flex: 0 1 50%;
    display: flex;
    flex-direction: column;

    @media (min-width:em(621)) {
      flex: 0 1 61%;
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__logo {
    display: inline-flex;

    @media (max-width: em(620)) {
      margin-bottom: rem(16);
    }
  }

  &__info {
    @media (max-width: em(620)) {
      padding-top: rem(30);
    }
  }

  &__social {
    flex: 0 1 50%;

    @media (min-width:em(620)) {
      flex: 0 1 39%;
    }
  }
}

.menu-footer {

  &__list {
    display: flex;
    flex-direction: row;
    @include adaptiveValue("gap", 42, 20, 1490, 620, 0);

    @media (max-width:em(620)) {
      flex-direction: column;
      gap: rem(8);
    }
  }

  &__item {
    @media (any-hover: hover) {
      &:hover {
        .menu-footer__link {
          color: $cBtn;
        }
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: rem(14);
    line-height: math.div(21, 14);
    color: $cWhite;
    transition: color 0.3s;
  }
}

.social {
  display: flex;
  @include adaptiveValue("gap", 28, 22);

  &__link {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: $cWhite;
    @include adaptiveValue("font-size", 16, 12);
    padding: rem(4);
    margin: rem(-4);
    transition: color 0.3s;

    @media (any-hover: hover) {
      &:hover {
        color: $cBtn;
      }
    }
  }
}
