.title-section {
  font-weight: 900;
  @include adaptiveValue("font-size", 36, 21);
  line-height: math.div(44, 36);
  color: #fff;
  text-transform: uppercase;

  & span {
    font-weight: 400;
    color: $cBtn;
  }
}

.subtitle-section {
  @include adaptiveValue("font-size", 14, 12);
  font-weight: 400;
  line-height: math.div(17, 14);
  color: $cBtn;
}

.text-section {
  line-height: math.div(24, 16);
  font-weight: 400;
  color: $cTextMain;
}

.title-card {
  font-weight: 900;
  font-size: rem(18);
  line-height: math.div(19, 18);
  color: $cWhite;
  text-transform: uppercase;
}

.logo {
  font-size: 0;
  position: relative;
  z-index: 7;
}

.icon-menu {
  display: block;
  position: relative;
  z-index: 8;
  width: rem(24);
  height: rem(14);
  background-color: transparent;

  @media (any-hover: none) {
    cursor: default;
  }

  @media (min-width:em(768.02)) {
    display: none;

    &--order {
      display: block;
    }
  }

  span,
  &::before,
  &::after {
    content: "";
    position: absolute;
    // z-index: 5;
    height: rem(2);
    right: 0;
    background-color: $cWhite;
    transition: all 0.3s ease 0s;
  }

  span {
    width: percent(18, 24);
    top: calc(50% - rem(1));
    right: percent(12, 24);
  }

  &::before,
  &::after {
    width: 100%;
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }

  &._menu-open {

    span {
      transform: scale(0);
    }

    &::before {
      top: calc(50% - rem(1));
      transform: rotate(-45deg);
    }

    &::after {
      bottom: calc(50% - rem(1));
      transform: rotate(45deg);
    }
  }
}

.card-more {
  @media (max-width:$mobileSmall) {
    overflow: hidden;
  }

  &__icon-ibg-contain {
    max-height: rem(84);
    @include adaptiveValue("margin-bottom", 30, 20);
    overflow: visible;
    transition: transform 0.3s;

    &::before {
      content: "";
      display: block;
      padding-bottom: percent(94, 346);
    }

    @media (max-width:$mobileSmall) {
      display: none;
    }
  }

  &__title {
    @include adaptiveValue("font-size", 18, 16, 1490, 767.98, 1);
    transition: color 0.3s;

    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 20, 10);
    }
  }

  &__text {
    transition: color 0.3s;

    &--more-than {
      @media (max-width:$mobileSmall) {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
  
      @media (min-width:$mobileSmall) {
        padding: 0 rem(7);
      }
    }

    &--benefits {
      @media (max-width: em(768)) {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
      }
    }
  }

  &--more-than {
    @media (max-width:$mobileSmall) {
      height: rem(50);

      & span {
        padding-right: rem(42);
      }
    }
  }

  &--benefits {
    @media (max-width:$mobileSmall) {
      height: rem(70);

      & span {
        padding-right: rem(77);
      }
    }
  }

  @media (any-hover: hover) {
    &:hover {
      .card-more__icon-ibg-contain {
        overflow: visible;
        transform: scale(1.1);
      }

      .card-more__title {
        color: $cTextMain;
      }

      .card-more__text {
        color: $cWhite;
      }
    }
  }
}

.video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
  transform: scale(0);
  transition: all 0.3s;

  &._active {
    position: fixed;
    z-index: 7;
    overflow: visible;
    opacity: 1;
    transform: scale(1);
  }

  &__body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100%;
    padding: rem(30) rem(20);
  }

  &__item {
    position: relative;
    flex: 0 1 rem(876);
    max-height: rem(486);
    border-radius: rem(8);
    overflow: hidden;

    &::after {
      content: "";
      display: block;
      padding-bottom: percent(486, 876);
    }

    & iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__icon-cross {
    display: inline-block;
    position: absolute;
    z-index: 8;
    top: rem(16);
    right: rem(14);
    width: rem(24);
    height: rem(14);
    background-color: transparent;

    @media (max-width:em(564.98)) {
      top: rem(8);
      right: rem(4);
    }

    @media (any-hover: none) {
      cursor: default;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      z-index: 9;
      height: rem(2);
      width: 100%;
      right: 0;
      background-color: $cWhite;
      transition: all 0.3s ease 0s;
    }

    &::before {
      top: calc(50% - rem(1));
      transform: rotate(-45deg);
    }

    &::after {
      bottom: calc(50% - rem(1));
      transform: rotate(45deg);
    }
  }
}
