.label {
  font-weight: 400;
  line-height: math.div(21, 16);
  color: $cTextMain;
  @include adaptiveValue("margin-bottom", 20, 10);
}

.order {
  position: absolute;
  top: -100%;
  left: 0;
  right: 0;
  transition: all 0.3s linear;

  &._open {
    position: fixed;
    top: 0;
    height: 100%;
    overflow-y: auto;
    z-index: 10;
    background: linear-gradient(107.56deg, #191536 0%, #000 100%);
  }

  &__body {
    padding-left: rem(20);
    padding-right: rem(20);

  }

  &__card {
    position: absolute;
    left: 0;
    right: 0;
    bottom: -100%;
    padding-bottom: rem(30);
    transform: scale(0);
    transition: all 0.3s;

    &--placeorder,
    &--pay {
      @include adaptiveValue("padding-top", 160, 30);

      @media (min-width:em(1280)) {
        display: flex;
      }
    }

    &--completed {
      @include adaptiveValue("padding-top", 178, 30);
    }

    &._active {
      position: relative;
      bottom: 0;
      transform: scale(1);
    }

    &:has(._select-open) {
      padding-bottom: rem(140);
    }
  }
}

.header-order {
  &__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include adaptiveValue("padding-top", 58, 16);
    @include adaptiveValue("padding-bottom", 58, 16);
  }

  &__steps {
    flex: 1 1 auto;
  }

  &__icon-menu {
    display: inline-block;
  }
}

.steps-order {
  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include adaptiveValue("padding-left", 214, 90, 1490, 767.98, 0);
    @include adaptiveValue("padding-right", 214, 90, 1490, 767.98, 0);
  }

  &__item {
    @media (max-width:$mobile) {
      flex: 0 1 33.33%;
      text-align: center;
      border-bottom: solid rem(1) #545454;
      transition: border-color 0.3s;
    }

    @media (max-width:$mobileSmall) {
      &:first-child {
        text-align: left;
        padding-left: rem(20);
      }

      &:last-child {
        text-align: right;
        padding-right: rem(20);
      }
    }

    @media (any-hover: hover) {
      &:hover {
        .steps-order__link {
          color: $cWhite;
        }
      }
    }

    .steps-order__link {
      display: inline-block;
      font-weight: 500;
      font-size: rem(14);
      line-height: math.div(18, 14);
      text-transform: capitalize;
      color: #545454;
      transition: color 0.3s;

      @media (max-width:$mobileSmall) {
        font-size: rem(12);
        line-height: math.div(18, 12);
      }

      & span {
        @media (max-width:$mobile) {
          display: none;
        }
      }
    }

    &._active,
    &:active {
      @media (max-width:$mobile) {
        border-bottom: solid rem(1) $cBtn;
      }

      .steps-order__link {
        color: $cBtn;

        @media (min-width:$mobile) {
          &::after {
            content: "";
            position: relative;
            left: 50%;
            top: rem(4);
            transform: translateX(-50%);
            display: block;
            @include square(rem(4));
            border-radius: 50%;
            background-color: $cBtn;
          }
        }
      }
    }
  }
}

.card-product {
  position: relative;
  flex: 0 1 50%;

  &__image {
    padding-bottom: percent(446, 960);

    @media (max-width:$mobile) {
      padding-bottom: percent(277, 524);
    }

    @media (max-width:$mobileSmall) {
      display: none;
    }
  }

  &__content {
    position: relative;
    display: flex;
    justify-content: space-between;
    @include adaptiveValue("width", 348, 244, 767.98, 320, 1);
    @include adaptiveValue("margin-bottom", 40, 10, 1279.98, 320, 0);
    @include adaptiveValue("margin-left", 352, 0, 1279.98, 594, 0);

    @media (max-width:$mobileSmall) {
      margin-left: auto;
      margin-right: auto;
    }

    @media (min-width:em(1279.98)) {
      position: absolute;
      bottom: 0;
      left: percent(235, 960);
      column-gap: rem(94);
      max-width: rem(294);
    }
  }
}

.price {
  &__curent {
    @include adaptiveValue("font-size", 36, 26);
    @include adaptiveValue("line-height", 44, 31);
    color: $cBtn;
  }
}

.completed {
  &__body {
    text-align: center;

    @media (max-width:em(768)) {
      max-width: rem(700);
      margin: 0 auto;
    }
  }

  &__title {
    @include adaptiveValue("margin-bottom", 56, 16);
    font-weight: 900;
    @include adaptiveValue("font-size", 46, 26, 767.98, 320, 1);
    line-height: 1.3;
    text-transform: uppercase;
  }

  &__text {
    @include adaptiveValue("margin-bottom", 70, 30);
    @include adaptiveValue("font-size", 18, 16);
    line-height: 1.3;
    font-weight: 400;
    color: $cWhite;
  }

  &__button {
    text-transform: none;
  }
}
