// <SELECT>
// Сніппет (HTML): sel
.select {
  min-width: 0;
  position: relative;

  // .select__body
  &__body {
    position: relative;
  }

  // .select__title
  &__title {
    font-family: $fontFamily;
    text-align: left;
    color: $cBtn;
    line-height: math.div(21, 16);
    font-weight: 500;
    cursor: pointer;
    width: 100%;
    padding: rem(4) rem(18) rem(6) rem(2);
    background-color: transparent;

    @media (any-hover: hover) {
      &:hover {

        .select__content,
        .select__value::after {
          color: $cWhite;
        }
      }
    }

    &:active {
      color: $cWhite;
    }
  }

  // .select__value
  &__value {
    display: flex;
    min-width: 0;
    align-items: center;
    gap: rem(10);
    transition: color 0.3s ease 0s;

    > * {
      flex: 1 1 auto;
    }

    &::after {
      @extend %ic;
      content: "\e902";
      position: absolute;
      right: rem(4);
      bottom: rem(11);
      font-size: rem(5);
      transition: all 0.3s ease 0s;

      ._select-open & {
        transform: rotate(-180deg);
      }
    }

    &._select-pseudo-label {
      &::before {
        content: attr(data-pseudo-label);
        opacity: 0.5;
      }
    }
  }

  // .select__content
  &__content {
    flex: 1 1 auto;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    transition: color 0.3s ease 0s;
  }

  // .select__text
  &__text {
    flex: 1 1 auto;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__input {
    width: 100%;
    background-color: transparent;
    height: 100%;
  }

  // .select__options
  &__options {
    color: $cBtn;
    position: absolute;
    top: calc(100% - rem(1));
    min-width: 100%;
    left: 0;
    // background-color: hsla(249, 47%, 3%, 0.9);
    // background-color: hsla(247, 44%, 15%, 0.9);
    background-color: $cBgMenu;

    border-radius: rem(4);
  }

  // .select__scroll
  &__scroll {
    overflow-y: auto;
    overflow-x: hidden;
    // Максимальна висота
    max-height: 200px;
    padding: rem(5) 0;

    // Стилі скроллбару
    .simplebar-wrapper {
      border-radius: rem(4);
    }

    .simplebar-track {
      background-color: #014954;
      max-width: rem(2);
      text-align: center;

      .simplebar-scrollbar {
        position: relative;

        &::before {
          position: absolute;
          left: 0;
          opacity: 1;
          background-color: $cBtn;
          width: rem(2);
        }
      }
    }
  }

  // .select__option
  &__option {
    width: 100%;
    text-align: center;
    cursor: pointer;
    padding: rem(6) rem(4);
    color: inherit;
    background-color: transparent;
    transition: color 0.3s ease 0s;

    &._select-selected {
      color: $cWhite;
    }

    @media (any-hover: hover) {
      &:hover {
        color: $cWhite;
      }
    }
  }

  // Конструкція додаткових даних
  &__row {
    display: inline-flex;
    align-items: center;
  }

  &--language {
    @media (max-width:em(768)) {
      display: none;
    }
  }

  &--quantity {

    & .select__title {
      padding: rem(10) rem(40);
      background-color: $cBgMenu;
      border-radius: rem(4);

      @media (any-hover: hover) {
        &:hover {
          .select__content {
            color: $cWhite;
          }

          .select__value::after {
            color: $cBtn;
          }
        }
      }

      &:active {
        color: $cWhite;
      }
    }

    & .select__value {
      display: inline-flex;
      justify-content: center;
      gap: rem(10);

      &::after {
        bottom: percent(20, 46);
        right: percent(10, 90);
        color: $cWhite;
      }
    }

    & .select__content {
      font-weight: 400;
      font-size: rem(20);
      line-height: math.div(26, 20);
      color: $cWhite;
      text-align: center;
    }

    & .select__options {
      padding-top: 0;
      margin-top: 3px;
      border-radius: rem(4);
    }

    & .select__option {
      font-weight: 400;
      font-size: rem(20);
      line-height: math.div(26, 20);
      color: $cWhite;

      &._select-selected {
        color: $cBtn;
      }

      @media (any-hover: hover) {
        &:hover {
          color: $cBtn;
        }
      }
    }
  }

  &--country,
  &--city {
    & .select__body {
      &::before {
        display: block;
        font-weight: 400;
        font-size: rem(14);
        line-height: math.div(21, 14);
        color: $cTextMain;
        margin-bottom: rem(12);
      }
    }

    & button {
      position: relative;
      background-color: $cBgMenu;
      padding: rem(12) rem(34) rem(12) rem(24);
      border-radius: rem(4);
      margin-bottom: rem(4);

      @media (any-hover: hover) {
        &:hover {

          .select__content {
            color: $cWhite;
          }

          .select__value::after {
            color: $cBtn;
          }
        }
      }

      &:active {
        color: $cWhite;
      }
    }

    & .select__value {
      &::after {
        font-size: rem(6);
        right: rem(16);
        bottom: rem(20);
      }
    }

    & .select__options {
      padding: rem(10) rem(18);
    }

    & .select__option {
      text-align: left;
    }
  }

  &--city {
    display: none;

    @media (min-width:$mobile) {
      display: block;
    }

    & .select__body {
      &::before {
        content: "City*";
      }
    }
  }

  &--country {
    & .select__body {
      &::before {
        content: "Country*";
      }
    }

    & + .select--city {
      display: none;

      @media (max-width:$mobile) {
        display: block;
      }
    }
  }

  // Стан селекту
  &._select-open {
    z-index: 5;
  }
}

// Тег
._select-tag {
  cursor: pointer;
}
