.help,
.faq {
  &__body {
    @media (max-width:$mobileSmall) {
      min-height: 100%;
    }

    @media (max-width:em(768)) {
      position: fixed;
      z-index: 7;
      left: -100%;
      top: 0;
      width: 100%;
      max-height: 100%;
      overflow-y: auto;
      background-color: $cBgMenu;
      transition: all 0.3s;

      &::before {
        content: "";
        position: fixed;
        z-index: 8;
        left: -100%;
        top: 0;
        width: 100%;
        @include adaptiveValue("height", 110, 50);
        background-color: $cBgMenu;
        transition: all 0.3s;
      }
    }

    @media (min-width:em(768.02)) {
      display: flex;
      justify-content: center;
      align-items: center;
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.7);
      transform: scale(0);
      transition: transform 0.3s;
    }

    &._sub-menu-open {
      @media (max-width:em(768)) {
        left: 0;

        &::before {
          left: 0;
        }
      }

      @media (min-width:em(768.02)) {
        z-index: 7;
        transform: scale(1);
      }
    }
  }

  &__content {
    position: relative;
    padding: 0 rem(20);
    max-height: 100%;
    overflow-y: auto;

    @media (max-width:em(768)) {
      @include adaptiveValue("padding-top", 100, 70);
      @include adaptiveValue("padding-bottom", 140, 20);
    }

    @media (min-width:em(768.02)) {
      width: percent(1080, 1920);
      @include adaptiveValue("padding-top", 50, 20);
      @include adaptiveValue("padding-bottom", 50, 20);
      background-color: $cBgMenu;
    }

    & .icon-menu {
      display: none;

      @media (min-width:em(768.02)) {
        display: block;
        position: absolute;
        top: rem(28);
        right: rem(28);
      }
    }
  }
}

.title-menu {
  font-weight: 900;
  font-size: rem(21);
  line-height: math.div(27, 21);
  color: $cWhite;

  & > a {
    font-weight: 400;
    font-size: rem(21);
    line-height: math.div(27, 21);
    color: $cBtn;
  }
}

.contacts-info {
  display: flex;
  flex-direction: column;

  @media (min-width:$mobileSmall) {
    display: inline-flex;
  }

  &__link {
    align-self: start;
    line-height: math.div(19, 16);
    color: $cTextMain;

    @media (min-width:$mobileSmall) {
      display: inline-flex;
    }
  }
}

.help {

  &__title {
    &:not(:last-child) {
      margin-bottom: rem(30);
    }
  }

  &__text {
    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 30, 20, 768, 479.98, 1);
    }

    color: $cTextMain;

    & a {
      color: $cBtn;
    }
  }

  &__instructions {
    display: flex;
    flex-direction: column;
    @include adaptiveValue("margin-right", 50, 40, 889.98, 479.98, 0);

    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 30, 20, 479.98, 320, 0);
    }

    @media (min-width:$mobileSmall) {
      display: inline-flex;
      @include adaptiveValue("margin-right", 152, 50, 1490, 890, 0);

      &:not(:last-child) {
        @include adaptiveValue("margin-bottom", 30, 20, 805, 768, 0);
      }
    }
  }

  &__contacts-info {
    & .contacts-info__link {
      &:not(:last-child) {
        @include adaptiveValue("margin-bottom", 24, 10, 1490, 479.98, 1);
      }
    }
  }

  &__link {
    align-self: start;
    color: $cBtn;

    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 20, 8, 1490, 479.98, 1);
    }
  }
}

.faq {

  &__title {
    margin-bottom: rem(30);
  }

  &__block {
    border: rem(1) solid $cBtn;
    border-radius: rem(4);

    &:not(:last-child) {
      margin-bottom: rem(12);
    }
  }

  &__question {
    color: $cBtn;
    cursor: default;
    padding: rem(18) rem(12);
    @include adaptiveValue("padding-right", 50, 35);

    ._spoller-init & {
      position: relative;
      width: 100%;
      text-align: left;
      cursor: pointer;

      &::before,
      &::after {
        content: "";
        display: block;
        position: absolute;
        right: rem(10);
        top: 50%;
        background-color: $cBtn;
        height: rem(2);
        @include adaptiveValue("width", 15, 10);
        transition: transform 0.5s ease 0s;
      }

      &::before {
        transform: translate(-75%, -50%) rotate(40deg);
      }

      &::after {
        transform: translate(0, -50%) rotate(-40deg);
      }

      &._spoller-active {
        &::before {
          transform: translateX(-75%) rotate(-40deg);
        }

        &::after {
          transform: rotate(40deg);
        }
      }
    }
  }

  &__answer {
    padding: 0 rem(12) rem(12);
  }

  &__text {
    color: $cTextMain;
    @include adaptiveValue("margin-bottom", 20, 10);
  }

  &__date {
    font-size: rem(12);
    color: #545454;
  }
}
