.home {
  background: linear-gradient(107.56deg, #191536 0%, #000 100%);
  background-repeat: no-repeat;

  &__body {
    display: grid;
    position: relative;

    @media (max-width:$tablet) {
      align-items: start;
      @include adaptiveValue("padding-top", 120, 50, 992, 320, 1);
      @include adaptiveValue("padding-bottom", 60, 20, 992, 320, 1);
    }

    @media (min-width:$tablet) {
      min-height: 100vh;
      padding-top: rem(150);
    }
  }

  &__block-top {
    display: flex;
    min-width: 0;

    @media (max-width: $mobile) {
      flex-direction: column;
    }

    @media (min-width:$tablet) {
      align-self: center;
    }
  }

  &__content {
    position: relative;
    z-index: 2;
    max-width: rem(552);

    @media (max-width:$mobile) {
      order: 2;
    }

    @media (min-width: $mobile) {
      flex: 0 0 percent(295, 700);
    }
  }

  &__title {
    @include adaptiveValue("margin-bottom", 36, 18);
    @include adaptiveValue("font-size", 54, 26, 1490, 768, 1);
    font-weight: 900;
    line-height: math.div(34, 26);
    color: $cWhite;
    white-space: nowrap;

    @media (min-width: em(1280)) {
      line-height: math.div(60, 46);
    }

    @media (min-width:$pc) {
      line-height: math.div(70, 54);
    }

    & span {
      font-weight: 400;
      color: $cBtn;
    }
  }

  &__info {
    @include adaptiveValue("padding-left", 88, 74, 1490, 1280, 2);
    @include adaptiveValue("padding-right", 88, 74, 1490, 1280, 2);
  }

  &__text {
    font-weight: 500;
    line-height: math.div(24, 16);
    color: $cTextMain;

    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 30, 8);
    }

    @media (min-width:$pc) {
      font-size: rem(20);
      line-height: math.div(30, 20);
    }
  }

  &__price {
    @include adaptiveValue("margin-bottom", 36, 16);
    font-weight: 500;
    font-size: rem(18);
    line-height: math.div(22, 16);
    color: $cWhite;
  }

  &__button-play {
    @media (min-width:em(1280.02)) {
      margin-left: rem(10);
    }
  }

  &__slider {
    @media (min-width:$mobile) {
      flex: 1 1 auto;
    }
  }

  &__bottom-action {
    align-self: center;
  }
}

.bottom-action {
  display: none;

  @media (min-width:$tablet) {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    font-weight: 500;
    line-height: math.div(24, 16);
    padding: rem(20) 0;
  }

  &__show-more {
    display: inline-flex;
    flex-direction: column-reverse;
    font-weight: 500;
    line-height: math.div(24, 16);
    color: $cBtn;
    transition: color 0.3s ease 0s;

    &::before {
      font-size: rem(6);
      text-align: center;
      color: $cBtn;
      padding-top: 8px;
      transition: color 0.3s ease 0s;
    }

    @media (any-hover: hover) {
      &:hover {
        color: $cWhite;

        &::before {
          color: $cWhite;
        }
      }
    }
  }
}

.menu-help {
  &__list {
    display: flex;
    align-items: center;
    gap: rem(40);
  }

  &__item {
    @media (any-hover: hover) {
      &:hover {
        .menu-help__link {
          color: $cBtn;
        }
      }
    }
  }

  &__link {
    color: $cWhite;
    transition: color 0.3s;
  }
}

.card-home {
  @media (max-width:$mobile) {
    order: 1;
  }

  &__image-ibg {
    z-index: 1;
    transition: all 0.3s;

    @media (max-width:$mobile) {
      max-width: rem(400);
      margin: 0 auto;
    }

    &::before {
      content: "";
      display: block;
      padding-bottom: percent(486, 898);
    }

    @media (any-hover: hover) {
      &:hover {
        transform: scale(0.9);
      }
    }
  }
}
