.header {
  position: fixed;
  z-index: 6;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  background: linear-gradient(
    107.56deg, hsla(247, 44%, 15%, 0.8) 0%, hsla(0, 0%, 0%, 0.7) 100%
  );

  &__body {
    display: flex;
    align-items: center;
    padding: rem(10) 0;
    @include adaptiveValue("min-height", 120, 50);

    @media (max-width:em(768)) {
      justify-content: space-between;
    }
  }

  &__logo {
    margin-right: rem(34);
  }

  &__menu {
    margin-left: auto;
    @include adaptiveValue("margin-right", 36, 20, 1490, 780, 1);
  }

  &__button {
    @media (max-width:em(768)) {
      display: none;
    }
  }
}

.menu {
  &__body {

    @media (max-width:em(768)) {
      display: block;
      position: fixed;
      z-index: 7;
      left: -100%;
      top: 0;
      width: 100%;
      max-height: 100%;
      @include adaptiveValue("padding-top", 100, 70);
      @include adaptiveValue("padding-bottom", 140, 20);
      background-color: $cBgMenu;
      overflow-y: auto;
      transition: all 0.3s;

      @media (max-width:$mobileSmall) {
        min-height: 100%;
      }

      &::before {
        content: "";
        position: fixed;
        z-index: 8;
        left: -100%;
        width: 100%;
        top: 0;
        @include adaptiveValue("height", 140, 50);
        background-color: $cBgMenu;
        transition: all 0.3s;
      }

      &._menu-open {
        left: 0;

        &::before {
          left: 0;
        }
      }
    }

    &--language {
      z-index: 9;

      @media (max-width:em(768)) {
        &._sub-menu-open {
          left: 0;
        }
      }

      @media (min-width:em(768.02)) {
        display: none;
      }
    }
  }

  &__list {
    display: flex;
    flex-direction: column;

    @media (min-width:em(768.02)) {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      @include adaptiveValue("column-gap", 36, 20, 1490, 780, 1);
      row-gap: rem(5);
    }
  }

  &__arrow-back {
    position: absolute;
    z-index: 10;
    @include adaptiveValue("top", 34, 20, 768, 320, 3);
    @include adaptiveValue("left", 34, 20, 768, 320, 3);
    margin: rem(-4);

    &::before {
      display: inline-block;
      padding: rem(4);
      font-size: rem(17);
      color: $cWhite;
      cursor: pointer;
    }
  }

  &__item {
    transition: color 0.3s ease 0s;

    @media (any-hover: hover) {
      &:hover,
      &:active {
        @media (max-width:em(768)) {
          background-color: #110e25;
        }

        .menu__link {
          color: $cBtn;
        }
      }
    }

    // &:active {
    //   @media (max-width:$mobile) {
    //     background-color: #110e25;
    //   }

    //   .menu__link {
    //     color: $cBtn;
    //   }
    // }

    &--lang,
    &--faq,
    &--help {
      @media (min-width:em(768.02)) {
        display: none;
      }
    }
  }

  &__link {
    display: block;
    font-weight: 500;
    font-size: rem(21);
    line-height: math.div(27, 21);
    color: $cWhite;
    transition: color 0.3s ease 0s;

    @media (max-width:em(768)) {
      @include adaptiveValue("padding-left", 152, 20, 768, 320, 1);
      padding-right: rem(20);
      padding-top: rem(12);
      padding-bottom: rem(12);
    }

    @media (min-width:em(768.02)) {
      font-size: rem(16);
      line-height: math.div(21, 16);
    }
  }
}
