.contacts {
  background-color: $cBgBlack;

  &__body {
    @include adaptiveValue("padding-top", 156, 50);
    @include adaptiveValue("padding-bottom", 200, 50);

    @media (min-width:em(679.98)) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__content {
    padding-right: rem(20);
    padding-top: rem(20);

    @media (max-width: em(680)) {
      &:not(:last-child) {
        margin-bottom: em(32, 21);
      }
    }
  }

  &__subtitle {
    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 14, 10);
    }
  }

  &__title {
    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 26, 14);
    }
  }

  &__text {
    @media (max-width:$mobileSmall) {
      font-size: rem(12);
    }

    @media (min-width: em(679.98)) {
      &:not(:last-child) {
        margin-bottom: em(76, 16);
      }
    }
  }

  &__info {
    @media (max-width: em(679.98)) {
      margin-bottom: em(22, 13);
    }

    & .contacts-info__link {
      transition: color 0.3s;

      @media (max-width: em(679.98)) {
        font-size: rem(13);
        line-height: math.div(20, 13);
      }

      @media (any-hover: hover) {
        &:hover {
          color: $cBtn;
        }
      }

      &:not(:last-child) {
        margin-bottom: em(10, 16);
      }
    }
  }

  &__form {

    @media (max-width: em(679.98)) {
      &:not(:last-child) {
        margin-bottom: rem(30);
      }
    }
  }
}
