.benefits {
  background: linear-gradient(146.86deg, #191536 -0.5%, #000 99.5%);

  &__body {
    @include adaptiveValue("padding-top", 160, 50);
    @include adaptiveValue("padding-bottom", 160, 50);
  }

  &__title {
    @include adaptiveValue("margin-bottom", 82, 32);

    @media (min-width:$tablet) {
      text-align: center;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @include adaptiveValue("column-gap", 206, 30, 1490, 768, 1);
    @include adaptiveValue("row-gap", 40, 20);
    @include adaptiveValue("margin-bottom", 90, 30, 1280, 769, 0);

    @media (max-width: $mobile) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__card {
    @media (min-width:$mobileSmall) {
      text-align: center;
    }
  }

  &__button {
    display: none;

    @media (min-width:em(769)) {
      display: block;
      margin: 0 auto;
    }

    @media (min-width: em(1281)) {
      display: none;
    }
  }
}
