%ic {
  font-family: "icon-font", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class*="_icon-"] {
  &:before {
    @extend %ic;
    display: block;
  }
}

._icon-arrow-left:before {
  content: "\e900";
}

._icon-down:before {
  content: "\e902";
}

._icon-facebook:before {
  content: "\e904";
}

._icon-play:before {
  content: "\e907";
}

._icon-reddit:before {
  content: "\e908";
}

._icon-twitter:before {
  content: "\e909";
}

._icon-up:before {
  content: "\e90a";
}

._icon-you-tube:before {
  content: "\e90d";
}
