.up {
  position: fixed;
  text-align: center;
  bottom: percent(190, 762);
  right: calc((100vw - 1450px) / 2);
  z-index: 2;
  display: none;
  @include square(rem(50));
  border-radius: 50%;
  border: rem(1) solid $cBtn;
  transition: all 0.3s;

  @media (max-width:em(1490)) {
    right: em(20);
  }

  @media (max-width:em(1150)) {
    bottom: percent(172, 682);
  }

  &._icon-up::before {
    font-size: rem(12);
    color: $cBtn;
    line-height: em(50, 12);
    transition: all 0.3s;
  }

  &._up-show {
    display: block;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  }

  @media (any-hover: hover) {
    &:hover {
      cursor: pointer;
      background-color: $cBtn;

      &._icon-up::before {
        color: #fff;
        transform: scale(1.1);
      }
    }
  }

  &:active {
    background-color: $cBtn;

    &._icon-up::before {
      color: #fff;
      transform: scale(1.1);
    }
  }
}
