.about {
  background: linear-gradient(146.86deg, #191536 -0.5%, #000 99.5%);

  &__body {
    @include adaptiveValue("padding-top", 150, 50);
    @include adaptiveValue("padding-bottom", 150, 50);
  }

  &__about-product {
    @include adaptiveValue("margin-bottom", 176, 50);
  }
}

.about-product {
  @include adaptiveValue("padding-left", 230, 0, 1490, 992, 1);

  &__body {
    @media (min-width:$mobile) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }

    @media (min-width:$tablet) {
      grid-template-columns: rem(45) 1fr percent(437, 1220);
      grid-template-rows: 1fr rem(45);
    }
  }

  &__fraction {
    display: none;

    @media (min-width:$tablet) {
      display: block;
      grid-row: 1 / span 2;
      justify-self: start;
      font-size: rem(14);
      line-height: math.div(17, 14);
      font-weight: 400;
      color: $cWhite;
    }
  }

  &__slider {
    height: 100%;

    @media (min-width:$mobile) {
      max-width: rem(622);
    }

    &--m {
      display: block;

      @media (min-width:$tablet) {
        display: none;
      }
    }

    &--d {
      display: none;

      @media (min-width:$tablet) {
        display: block;
      }
    }
  }

  &__slide {
    min-height: rem(190);

    @media (max-width:$mobile) {
      max-height: rem(270);
    }
  }

  &__bullets {
    @media (max-width:$tablet) {
      grid-row: 2 / 3;
      position: relative;
      display: flex;
      justify-content: center;
      gap: rem(4);
      padding-top: rem(10);
      cursor: pointer;

      .swiper-pagination-bullet {
        position: relative;
        width: em(20);
        height: em(20);

        &::after {
          content: "";
          position: absolute;
          width: em(8);
          height: em(8);
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          border-radius: 50%;
          background-color: #484848;

        }
      }

      .swiper-pagination-bullet-active {
        &::after {
          content: "";
          background-color: #ababab;
        }
      }
    }

    display: none;
  }

  &__image-ibg {
    height: 100%;
    transition: transform 0.3s;

    &::before {
      content: "";
      display: block;
      padding-bottom: percent(290, 622);
    }

    & img {
      transition: transform 0.3s;
    }

    @media (any-hover: hover) {
      &:hover {
        & img {
          transform: scale(1.1);
        }
      }
    }
  }

  &__video {
    display: none;

    &._active {
      & + img {
        display: none;
      }

      display: block;
    }
  }

  &__content {
    @media (max-width:$mobile) {
      padding-top: rem(20);
    }

    @media (min-width:$mobile) {
      padding-left: rem(20);
    }
  }

  &__title {
    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 60, 30, 1490, 767.98, 1);
    }
  }

  &__text {
    &:not(:last-child) {
      margin-bottom: rem(20);
    }
  }

  &__button-play {
    margin: rem(0);
  }

  &__navigation {
    display: none;

    @media (min-width:$tablet) {
      display: block;
      padding-top: rem(10);
    }
  }
}

.about-us {

  &__body {
    @media (min-width:$mobile) {
      display: grid;
      grid-template-columns: rem(460) 1fr;
    }

    @media (min-width:$tablet) {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__content {
    place-self: center;
    max-width: rem(532);
  }

  &__subtitle {
    margin-bottom: em(14, 10);
  }

  &__title {
    @include adaptiveValue("margin-bottom", 32, 20);
  }

  &__lable-content {
    display: none;

    @media (min-width:$mobile) {
      display: grid;
      justify-content: space-between;
      grid-template-columns: repeat(3, 1fr);
    }

    @media (min-width:em(825)) {
      grid-template-columns: repeat(5, 1fr);
    }

    @media (min-width:em(1280)) {
      grid-template-columns: repeat(7, 1fr);
    }
  }

  &__image-ibg-contain {
    place-self: center;
    @include adaptiveValue("height", 344, 232, 1490, 767.98, 1);
    @include adaptiveValue("width", 84, 62, 1490, 767.98, 1);
    transition: opacity 0.3s;

    &:nth-child(2n) {
      transform: rotate(-180deg);
    }

    @for $i from 0 through 7 {
      &:nth-child(#{$i + 1}) {
        opacity: math.div(1, ($i * 2));
      }
    }

    @media (max-width:em(825)) {
      &:nth-child(2n+3) {
        display: none;
      }
    }

    @media (max-width:em(1280)) {
      &:nth-child(3n) {
        display: none;
      }
    }

    @media (any-hover: hover) {
      &:hover {
        opacity: 0.8;
      }
    }
  }
}
