.more-than {

  &__body {
    @include adaptiveValue("padding-top", 158, 50);
    @include adaptiveValue("padding-bottom", 158, 50);

    @media (min-width:$mobileSmall) {
      text-align: center;
    }
  }

  &__title {
    @include adaptiveValue("margin-bottom", 16, 12);
  }

  &__subtitle {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    @include adaptiveValue("width", 328, 280);
    margin: 0 auto;

    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 70, 40);
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: rem(20);

    @media (min-width:$mobileSmall) {
      row-gap: rem(40);
    }

    @media (max-width: em(1065)) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: $mobile) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}
