.swiper {
  overflow: hidden;
}

.swiper-wrapper {
  width: 100%;
  height: 100%;
  box-sizing: content-box;
  display: flex;
  position: relative;
}

.swiper-vertical {
  .swiper-wrapper {
    flex-direction: column;
  }
}

.swiper-autoheight {
  .swiper-wrapper {
    align-items: flex-start;
  }
}

.swiper-initialized {
  .swiper-slide {
    flex-shrink: 0;
    // transition-property: transform;
  }
}

.swiper-android {

  .swiper-slide,
  .swiper-wrapper {
    transform: translate3d(0, 0, 0);
  }
}

.swiper-button-lock {
  display: none !important;
}

.swiper-pagination-progressbar,
.swiper-pagination-progressbar-fill {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

.swiper-pagination-progressbar {
  height: rem(1);
  background-color: #212121;
  transition: all 0.3s;
  transform: translate3d(0, 0, 0);
}

.swiper-pagination-progressbar-fill {
  height: 100%;
  transform: scale(0);
  transform-origin: left top;
  background-color: $cBtn;
}

.swiper-button-disabled {
  color: $cTextMain;
  cursor: default;
}

.navigation-slider {
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: rem(102);
  color: $cWhite;

  &__prev,
  &__next {
    cursor: pointer;
    padding: rem(6) rem(6) rem(10);
    margin: rem(-6) rem(-6) rem(0);
  }
}
