.button {
  font-weight: 500;
  font-size: rem(16);
  line-height: math.div(21, 16);
  text-align: center;
  color: $cWhite;
  min-width: rem(200);
  padding: rem(13) 0;
  border-radius: em(4, 16);
  background-color: $cBtn;
  text-transform: capitalize;
  transition: all 0.3s;

  &__container {
    display: none;

    @media (min-width:$mobileSmall) {
      display: flex;
      justify-content: center;
      position: sticky;
      z-index: 3;
      top: calc(100vh - rem(105));
    }

    @media (min-width:em(768.02)) {
      display: none;
    }
  }

  &--page {
    @media (min-width:$mobileSmall) {
      width: 100%;
      max-width: rem(458);
      transform: translateY(-50%);
    }
  }

  &--order {
    text-transform: none;
    @media (max-width:em(1279.98)) {
      display: block;
      width: 100%;
      max-width: rem(460);
      margin: 0 auto;
    }
  }

  @media (any-hover: hover) {
    &:hover {
      color: $cBtn;
      background-color: $cWhite;
    }
  }

  &:active {
    color: $cBtn;
    background-color: $cWhite;
  }

  &:disabled {
    color: $cDarkGrey;
    background-color: #191536;
  }
}

.button-play {
  display: flex;
  align-items: center;
  width: rem(220);
  height: rem(70);
  background-color: transparent;
  background-image: url("../images/home/play.svg");
  background-repeat: no-repeat;
  background-position: 0 0;

  @media (max-width:$mobile) {
    margin-bottom: rem(24);
  }

  @media (min-width:$mobile) {
    width: rem(248);
  }

  &__btn {
    position: relative;
    z-index: 1;
    font-size: rem(21);
    color: $cBtn;
    left: percent(27, 220);

    @media (min-width:$mobile) {
      left: percent(27, 248);
    }

    &::before {
      transition: color 0.3s ease 0s;
    }
  }

  &__text {
    position: relative;
    z-index: 1;
    left: percent(106, 248);
    font-weight: 500;
    line-height: math.div(21, 16);
    color: $cWhite;
    transition: color 0.3s;
  }

  @media (any-hover: hover) {
    &:hover {
      .button-play__btn::before {
        color: $cWhite;
      }

      .button-play__text {
        color: $cBtn;
      }
    }
  }

  &:active {
    .button-play__btn::before {
      color: $cWhite;
    }

    .button-play__text {
      color: $cBtn;
    }
  }
}
